@font-face {
  font-family: 'Zilla Slab';
  src: local('Zilla Slab'), local('ZillaSlab-Regular'), url('/assets/fonts/ZillaSlabRegular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Zilla Slab';
  src: local('Zilla Slab Bold'), local('ZillaSlab-Bold'), url('/assets/fonts/ZillaSlabBold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

#root {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 60 60"><path d="M5 0L0 5V0h5zm10 0h10L0 25V15L15 0zM0 55l5 5H0v-5zm0-10V35l25 25H15L0 45zM60 5l-5-5h5v5zm0 10v10L35 0h10l15 15zM45 60H35l25-25v10L45 60zm10 0l5-5v5h-5zM30 5l25 25-5 5-20-20-20 20-5-5L30 5zm0 40l5 5-5 5-5-5 5-5zm0-20l15 15-5 5-10-10-10 10-5-5 15-15z"/></svg>');
  background-size: 20px;
  background-position: center 20px;
  background-color: #111;
}

body {
  font-family: 'Zilla Slab', sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.7)), color-stop(transparent), to(rgba(0, 0, 0, 0.7)));
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.7), transparent, rgba(0, 0, 0, 0.7));
  background: -o-linear-gradient(left, rgba(0, 0, 0, 0.7), transparent, rgba(0, 0, 0, 0.7));
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.7), transparent, rgba(0, 0, 0, 0.7));
}

html, body, #root, #App, #Game {
  height: 100%;
  padding: 0;
  margin: auto;
  overflow: hidden;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}


.hidden {
  display: none !important;
}


